import { FollowUpQuestionButton } from "@rp/react/web/chat"
import { Box, Fade } from "@rp/react/web/mui/core"

const DEFAULT_ENTITIES = 100
export const PARALLEL_ENTITIES_AMOUNT = Number(
  window.localStorage.getItem("PARALLEL_ENTITIES_AMOUNT") ?? DEFAULT_ENTITIES,
)

type Props = {
  entityOffset: number
  remainingEntities: number
  onAskForMore: (nextOffset: number) => void
}

export const WatchlistChatMoreActions = ({
  remainingEntities,
  entityOffset,
  onAskForMore,
}: Props) => {
  return (
    <Fade in style={{ transitionDelay: `200ms` }}>
      <Box>
        <FollowUpQuestionButton
          label={`Extend analysis to the next ${Math.min(
            PARALLEL_ENTITIES_AMOUNT,
            remainingEntities,
          )} constituents in your watchlist`}
          onClick={() => {
            onAskForMore(entityOffset + PARALLEL_ENTITIES_AMOUNT)
          }}
        />
      </Box>
    </Fade>
  )
}
